<template>
  <div class="account-settings">
    <div class="settings-section">
      <h3>‰øÆÊîπÂØÜÁ†Å</h3>
      <form @submit.prevent="handleChangePassword" class="form">
        <div class="form-group">
          <label for="currentPassword">ÂΩìÂâçÂØÜÁ†Å</label>
          <input
            id="currentPassword"
            v-model="passwordForm.currentPassword"
            type="password"
            placeholder="ËØ∑ËæìÂÖ•ÂΩìÂâçÂØÜÁ†Å"
            required
          />
        </div>
        
        <div class="form-group">
          <label for="newPassword">Êñ∞ÂØÜÁ†Å</label>
          <input
            id="newPassword"
            v-model="passwordForm.newPassword"
            type="password"
            placeholder="ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†ÅÔºàËá≥Â∞ë6‰ΩçÔºâ"
            required
            minlength="6"
          />
        </div>
        
        <div class="form-group">
          <label for="confirmPassword">Á°ÆËÆ§Êñ∞ÂØÜÁ†Å</label>
          <input
            id="confirmPassword"
            v-model="passwordForm.confirmPassword"
            type="password"
            placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
            required
          />
        </div>
        
        <div v-if="passwordError" class="error-message">
          {{ passwordError }}
        </div>
        
        <div v-if="passwordSuccess" class="success-message">
          {{ passwordSuccess }}
        </div>
        
        <button type="submit" :disabled="passwordLoading" class="btn-primary">
          {{ passwordLoading ? '‰øÆÊîπ‰∏≠...' : '‰øÆÊîπÂØÜÁ†Å' }}
        </button>
      </form>
    </div>

    <div class="settings-section danger-section">
      <h3>Ë¥¶Âè∑Ê≥®ÈîÄ</h3>
      <div class="warning-box">
        <div class="warning-icon">‚ö†Ô∏è</div>
        <div class="warning-content">
          <h4>Ë≠¶ÂëäÔºöÊ≠§Êìç‰Ωú‰∏çÂèØÈÄÜ</h4>
          <p>Âà†Èô§Ë¥¶Âè∑ÂêéÔºåÊÇ®ÁöÑÊâÄÊúâÊï∞ÊçÆÂ∞ÜË¢´Ê∞∏‰πÖÂà†Èô§ÔºåÂåÖÊã¨Ôºö</p>
          <ul>
            <li>‰∏™‰∫∫ËµÑÊñô‰ø°ÊÅØ</li>
            <li>ÂèëÂ∏ÉÁöÑÂ∏ñÂ≠êÂíåËØÑËÆ∫</li>
            <li>Êî∂ËóèÁöÑÂÜÖÂÆπ</li>
            <li>Ê∂àÊÅØËÆ∞ÂΩï</li>
          </ul>
          <p>ËØ∑Ë∞®ÊÖéÊìç‰ΩúÔºÅ</p>
        </div>
      </div>
      
      <form @submit.prevent="handleDeleteAccount" class="form">
        <div class="form-group">
          <label for="deletePassword">ËØ∑ËæìÂÖ•ÂØÜÁ†ÅÁ°ÆËÆ§Âà†Èô§</label>
          <input
            id="deletePassword"
            v-model="deleteForm.password"
            type="password"
            placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†ÅÁ°ÆËÆ§Âà†Èô§"
            required
          />
        </div>
        
        <div class="form-group">
          <label class="checkbox-label">
            <input
              v-model="deleteForm.confirm"
              type="checkbox"
              required
            />
            <span>ÊàëÁ°ÆËÆ§‰∫ÜËß£Âà†Èô§Ë¥¶Âè∑ÁöÑÂêéÊûúÔºåÂπ∂Ëá™ÊÑøÂà†Èô§Ë¥¶Âè∑</span>
          </label>
        </div>
        
        <div v-if="deleteError" class="error-message">
          {{ deleteError }}
        </div>
        
        <button 
          type="submit" 
          :disabled="deleteLoading || !deleteForm.confirm" 
          class="btn-danger"
        >
          {{ deleteLoading ? 'Âà†Èô§‰∏≠...' : 'Ê∞∏‰πÖÂà†Èô§Ë¥¶Âè∑' }}
        </button>
      </form>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { useAuthStore } from '@/stores/auth'

const router = useRouter()
const authStore = useAuthStore()

const passwordForm = ref({
  currentPassword: '',
  newPassword: '',
  confirmPassword: ''
})

const deleteForm = ref({
  password: '',
  confirm: false
})

const passwordLoading = ref(false)
const deleteLoading = ref(false)
const passwordError = ref('')
const passwordSuccess = ref('')
const deleteError = ref('')

const validatePassword = () => {
  if (passwordForm.value.newPassword.length < 6) {
    return 'Êñ∞ÂØÜÁ†ÅÈïøÂ∫¶Ëá≥Â∞ë6‰Ωç'
  }
  
  if (passwordForm.value.newPassword !== passwordForm.value.confirmPassword) {
    return '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥'
  }
  
  return null
}

const handleChangePassword = async () => {
  passwordError.value = ''
  passwordSuccess.value = ''
  
  const validationError = validatePassword()
  if (validationError) {
    passwordError.value = validationError
    return
  }
  
  passwordLoading.value = true
  
  try {
    const result = await authStore.changePassword(
      passwordForm.value.currentPassword,
      passwordForm.value.newPassword
    )
    
    if (result.success) {
      passwordSuccess.value = result.message || 'ÂØÜÁ†Å‰øÆÊîπÊàêÂäü'
      passwordForm.value = {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      }
    } else {
      passwordError.value = result.error || 'ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•'
    }
  } catch (error: any) {
    passwordError.value = error.message || 'ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•'
  } finally {
    passwordLoading.value = false
  }
}

const handleDeleteAccount = async () => {
  deleteError.value = ''
  
  if (!deleteForm.value.confirm) {
    deleteError.value = 'ËØ∑Á°ÆËÆ§Âà†Èô§Êìç‰Ωú'
    return
  }
  
  deleteLoading.value = true
  
  try {
    const result = await authStore.deleteAccount(deleteForm.value.password)
    
    if (result.success) {
      // Ë¥¶Âè∑Âà†Èô§ÊàêÂäüÔºåË∑≥ËΩ¨Âà∞È¶ñÈ°µ
      router.push('/')
    } else {
      deleteError.value = result.error || 'Ë¥¶Âè∑Âà†Èô§Â§±Ë¥•'
    }
  } catch (error: any) {
    deleteError.value = error.message || 'Ë¥¶Âè∑Âà†Èô§Â§±Ë¥•'
  } finally {
    deleteLoading.value = false
  }
}
</script>

